html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
a,
p,
span,
em,
small,
strong,
sub,
sup,
mark,
del,
ins,
strike,
abbr,
dfn,
blockquote,
q,
cite,
code,
pre,
ol,
ul,
li,
dl,
dt,
dd,
div,
section,
article,
main,
aside,
nav,
header,
hgroup,
footer,
img,
figure,
figcaption,
address,
time,
audio,
video,
canvas,
iframe,
details,
summary,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  line-height: 1.4;
}

* {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

a {
  color: inherit;
}

article,
aside,
footer,
header,
nav,
section,
main {
  display: block;
}

* {
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: inherit;
}

img {
  width: 100%;
  vertical-align: bottom;
}

ul,
li,
dl,
dt,
dd {
  list-style: none;
}

a {
  text-decoration: none;
}

input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

body::after {
  position: fixed;
  bottom: 10px;
  left: 30px;
  color: red;
  font-size: 50px;
  opacity: 0.5;
  z-index: 1000;
}
@media (max-width: 1400px) {
  body::after {
    content: "xl";
  }
}
@media (max-width: 1200px) {
  body::after {
    content: "lg";
  }
}
@media (max-width: 992px) {
  body::after {
    content: "md";
  }
}
@media (max-width: 768px) {
  body::after {
    content: "sm";
  }
}
@media (max-width: 576px) {
  body::after {
    content: "xsm";
  }
}

body {
  font-family: "Noto Sans JP", sans-serif;
  color: #4e301b;
}

.br_md, .br_sm {
  display: none;
}
@media (max-width: 992px) {
  .br_md {
    display: block;
  }
}
@media (max-width: 768px) {
  .br_sm {
    display: block;
  }
  .br_sm_hidden {
    display: none;
  }
}
@media (max-width: 576px) {
  .br_xsm {
    display: block;
  }
  .br_xsm_hidden {
    display: none;
  }
}

.mv {
  height: calc(100vh + 77px);
  padding-top: 112px;
  background: url(../../img/fv.jpg) no-repeat center/cover;
}
.mv_container {
  width: 78%;
  margin: 0 auto;
}
.mv h1 {
  margin-left: auto;
  font-size: 3.2rem;
  font-weight: 700;
  writing-mode: vertical-rl;
  letter-spacing: 0.5em;
  text-indent: 0.5em;
}
.mv h1 span {
  display: table;
  padding: 0 15px;
  background: #fff;
}
.mv h1 span:not(:last-of-type) {
  margin-left: 21px;
}

.header {
  position: relative;
  background: #fdf9ef;
  padding: 91px 0 84px;
}
.header_logo {
  position: absolute;
  top: -77px;
  left: calc(50% - 140px);
  left: 50%;
  width: 280px;
  padding-top: 30px;
  text-align: center;
  translate: -50% 0;
}
.header_logo::after {
  content: "";
  position: absolute;
  top: 0;
  left: calc(50% - 77px);
  right: auto;
  width: 154px;
  height: 154px;
  border-radius: 50%;
  background: #fdf9ef;
}
.header_logo_img {
  position: relative;
  width: 96px;
  margin-bottom: 7px;
  z-index: 10;
}
.header_nav_pc {
  position: relative;
  left: -30px;
}
.header_nav_pc ul {
  display: flex;
  justify-content: center;
}
.header_nav_pc_link {
  position: relative;
  font-weight: 700;
  z-index: 1;
}
.header_nav_pc_link:not(:last-of-type) {
  margin-right: 39px;
}
.header_nav_pc_link a::after {
  content: "";
  box-sizing: content-box;
  position: absolute;
  top: -5px;
  left: -10px;
  width: 100%;
  height: 100%;
  padding: 5px 10px;
  background: #fed966;
  z-index: -1;
  border-radius: 16px;
  scale: 0;
  transition: scale 0.6s;
}
.header_nav_pc_link a:hover::after {
  scale: 1;
}
.header_nav_pc_blank {
  width: 300px;
}
@media (max-width: 1200px) {
  .header {
    padding-top: 160px;
  }
  .header_nav_pc_blank {
    display: none;
  }
}

.about {
  position: relative;
  padding-top: 56px;
  padding-bottom: 62px;
  background: #fdf9ef;
}
.about_container {
  position: absolute;
  left: 50%;
  translate: -50% 0;
  width: min(67%, 924px);
  padding-top: 56px;
}
.about_title {
  display: inline-block;
  margin-bottom: 28px;
  padding: 9px;
  color: #35a1c3;
  font-size: 40px;
  line-height: 58px;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.9);
}
.about_text {
  font-size: 16px;
  line-height: 2.5;
}
.about_text_wrapper {
  display: inline-block;
  padding: 15px;
  background: rgba(255, 255, 255, 0.9);
}
.about_bg_wrapper {
  width: min(100% - 86px, 1200px);
  margin-inline: auto;
}
.about_bg_img {
  display: block;
  margin-left: auto;
  width: 66%;
}
.about_bg_img:last-of-type {
  width: 33%;
  margin-top: 38px;
}
@media (max-width: 1200px) {
  .about_container {
    width: 80%;
  }
  .about_bg_img {
    width: 80%;
  }
  .about_bg_img:last-of-type {
    width: 40%;
  }
}
@media (max-width: 992px) {
  .about {
    background: url(../../img/about-01.jpg) no-repeat left/cover;
  }
  .about::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.4);
  }
  .about_container {
    position: relative;
    width: 90%;
    z-index: 1;
  }
  .about_bg_wrapper {
    display: none;
  }
}
@media (max-width: 768px) {
  .about_title {
    font-size: 33px;
  }
}

.product {
  padding-top: 126px;
  background: #fdf9ef;
}
.product_title {
  font-size: 32px;
  font-weight: 700;
  text-align: center;
}
.product_slide {
  width: 100%;
  margin: 91px 0 94px;
}
.product_card {
  display: flex;
  flex-direction: column;
  height: auto;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.product_card_img {
  position: relative;
  width: 100%;
  padding-top: 62.9%;
}
.product_card_img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.product_card_content {
  display: flex;
  flex-direction: column;
  padding: 16px;
  height: 100%;
  background: #fff;
}
.product_card_content_text {
  font-weight: 700;
  line-height: 1.5;
  flex-grow: 1;
}
.product_card_content_price {
  margin-top: 14px;
  font-weight: 400;
  line-height: 23px;
}

.news {
  padding: 91px 0;
}
.news_title {
  font-size: 32px;
  font-weight: 700;
  line-height: 46px;
  text-align: center;
}
.news_container {
  width: min(67%, 934px);
  margin-inline: auto;
  margin-top: 90px;
}
.news_table {
  width: 100%;
  border-collapse: collapse;
}
.news_table tbody tr {
  border-top: 1px solid #c6c6c6;
}
.news_table tbody tr th,
.news_table tbody tr td {
  vertical-align: middle;
  padding: 25px 0;
}
.news_table tbody tr th {
  width: 20%;
  min-width: 120px;
  text-align-last: left;
  font-weight: 400;
}
.news_table tbody tr :last-of-type {
  border-bottom: 1px solid #c6c6c6;
}
@media (max-width: 992px) {
  .news_container {
    width: 90%;
  }
}

.faq {
  background: #fdf9ef;
  padding: 91px 0;
}
.faq_title {
  font-size: 32px;
  font-weight: 700;
  line-height: 46px;
  text-align: center;
}
.faq_container {
  width: min(58%, 740px);
  margin-inline: auto;
  margin-top: 73px;
}
.faq_item {
  width: 100%;
}
.faq_item_question {
  position: relative;
  margin-bottom: 31px;
  padding: 12px 14px;
  background: #fff;
  cursor: pointer;
}
.faq_item_question::before {
  content: "Q";
  margin-right: 8px;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 24px;
}
.faq_item_question::after {
  content: "";
  position: absolute;
  right: 14px;
  bottom: -23px;
  width: 16px;
  height: 16px;
  background: url(../../img/arrow.svg);
  rotate: 0;
  transition: rotate 0.6s ease;
  cursor: pointer;
}
.faq_item_answer {
  display: none;
  line-height: 24px;
  margin-bottom: 31px;
}
@media (max-width: 992px) {
  .faq_container {
    width: 90%;
  }
}

.js_is_open::after {
  rotate: 180deg;
}